import { paragraphTags } from '@ali/4ever-utils';
import type { JsonML, MoAttributes } from '@ali/4ever-cangjie';

export default function getParagraphTagStyle(
  name,
  rawAttrs,
): { p: JsonML; rawAttrs: MoAttributes } {
  if (['MsoTitle', 'MsoSubtitle'].includes(rawAttrs?.class)) {
    return {
      p: [paragraphTags.h2],
      rawAttrs: {
        style: `textAlign:center;${rawAttrs.style || ''}`,
        ...rawAttrs,
        styleObj: {
          ...(!rawAttrs.style ? { textAlign: 'center' } : {}),
          ...rawAttrs.styleObj,
        },
      },
    };
  }
  return {
    p: [paragraphTags[name]],
    rawAttrs,
  };
}
